"use client"
import React, { useState, useEffect } from "react";
import { Button } from "../ui/button";
import ThemeToggle from "./theme";
import { ethers } from "ethers";

export default function Navbar() {
  const [wallet, setWallet] = useState<string | null>(null);

  useEffect(() => {
    const checkWalletConnection = async () => {
      if (window.ethereum) {
        const provider = new Web3Provider(window.ethereum);
        const accounts = await provider.listAccounts();
        if (accounts.length > 0) {
          setWallet(accounts[0]);
        }
      }
    };

    checkWalletConnection();

    // Optionally, listen for account changes
    window.ethereum.on('accountsChanged', (accounts: string[]) => {
      if (accounts.length > 0) {
        setWallet(accounts[0]);
      } else {
        setWallet(null);
      }
    });
  }, []);

  const handleConnect = async () => {
    if (window.ethereum) {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const accounts = await provider.send("eth_requestAccounts", []);
      setWallet(accounts[0]);
    }
  };

  const handleDisconnect = () => {
    setWallet(null); // Disconnect the wallet by resetting the state
  };

  return (
    <nav className="shadow-md">
      <div className="max-w-screen-xl flex flex-wrap items-center justify-between mx-auto p-4">
        <Link href="/dashboard" className="self-center text-2xl font-semibold whitespace-nowrap dark:text-white">
          TokenFlow
        </Link>
      
        <div className="flex space-x-12">
          <Link href="/dashboard/payments" className="font-semibold hover:text-blue-700">
            Payment
          </Link>
          <Link href="/dashboard/transaction" className="font-semibold hover:text-blue-700">
            History
          </Link>
          <Link href="/dashboard/nft_Receipt" className="font-semibold hover:text-blue-700">
            Receipt
          </Link>
          <Link href="/dashboard/wallet" className="font-semibold hover:text-blue-700">
            Wallet
          </Link>
        </div>
        <div className="flex items-center gap-2">
          <ThemeToggle />
          <Button
            variant="outline"
            className="font-semibold"
            onClick={wallet ? handleDisconnect : handleConnect}
          >
            {wallet ? "Disconnect" : "Connect"}
          </Button>
        </div>
      </div>
    </nav>
  );
}
